'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'

interface AnimatedIntroProps {
  onComplete: () => void
}

export default function AnimatedIntro({ onComplete }: AnimatedIntroProps) {
  const [currentStep, setCurrentStep] = useState(0)
  const [isVisible, setIsVisible] = useState(true)

  const steps = [
    {
      title: "Bienvenido a Ankaly",
      subtitle: "Tu plataforma de entregas en el Pacífico",
      color: "from-teal-pacifico to-verde-manglar"
    },
    {
      title: "Conectamos Comunidades",
      subtitle: "Comercios locales al alcance de tu hogar",
      color: "from-coral-atardecer to-dorado-suave"
    },
    {
      title: "Entregas Rápidas y Seguras",
      subtitle: "Con el sabor auténtico de nuestra región",
      color: "from-verde-manglar to-teal-pacifico"
    }
  ]

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentStep((prev) => {
        if (prev < steps.length - 1) {
          return prev + 1
        } else {
          clearInterval(timer)
          setTimeout(() => {
            setIsVisible(false)
            setTimeout(onComplete, 500)
          }, 2000)
          return prev
        }
      })
    }, 2500)

    return () => clearInterval(timer)
  }, [onComplete, steps.length])

  const handleSkip = () => {
    setIsVisible(false)
    setTimeout(onComplete, 300)
  }

  if (!isVisible) {
    return (
      <div className="fixed inset-0 bg-gradient-to-br from-teal-pacifico to-verde-manglar opacity-0 transition-opacity duration-500 z-50" />
    )
  }

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      <div className={`absolute inset-0 bg-gradient-to-br ${steps[currentStep].color} transition-all duration-1000`} />
      
      <div className="relative z-10 text-center text-white px-6 max-w-2xl mx-auto">
        <div className="mb-8 animate-fade-in">
          <div className="h-20 w-20 mx-auto mb-6 rounded-full bg-white/20 backdrop-blur-sm flex items-center justify-center">
            <span className="text-white font-bold text-2xl">A</span>
          </div>
        </div>

        <h1 className="font-inter font-bold text-4xl md:text-6xl mb-4 animate-fade-in">
          {steps[currentStep].title}
        </h1>
        
        <p className="font-poppins text-xl md:text-2xl text-white/90 mb-8 animate-fade-in">
          {steps[currentStep].subtitle}
        </p>

        <div className="flex justify-center space-x-2 mb-8">
          {steps.map((_, index) => (
            <div
              key={index}
              className={`h-2 w-8 rounded-full transition-all duration-300 ${
                index === currentStep ? 'bg-white' : 'bg-white/30'
              }`}
            />
          ))}
        </div>

        <Button
          onClick={handleSkip}
          variant="ghost"
          className="text-white border-white/30 hover:bg-white/10 font-poppins"
        >
          Saltar Introducción
        </Button>
      </div>

      {/* Decorative Elements */}
      <div className="absolute top-10 left-10 w-20 h-20 rounded-full bg-white/10 animate-pulse" />
      <div className="absolute bottom-20 right-16 w-16 h-16 rounded-full bg-white/10 animate-pulse delay-300" />
      <div className="absolute top-1/3 right-10 w-12 h-12 rounded-full bg-white/10 animate-pulse delay-700" />
    </div>
  )
}
